.card {
    &__nav {
        margin-left: -7px;
        .sm-block({
            margin: 20px 0 0;
            width: 100%;
            order: 2;
        });
        .slick-track {
            display: flex;
            flex-direction: column;
            width: 72px !important;
            align-items: center;
            .sm-block({
                flex-direction: row;
                width: 100% !important;
                justify-content: center;
            });
        }
        .slick-list {
            padding: 0 !important;
        }
        &--item {
            width: 72px !important;
            height: 72px;
            border-radius: 3px;
            cursor: pointer;
            border: 1px solid #DFDFDF;
            display: flex;
            align-items: center;
            justify-content: center;
            &:not(:first-child) {
                margin-top: 21px;
                .sm-block({
                    margin-top: 0;
                    margin-left: 18px;
                });
            }
            img {
                width: 70px;
                height: 70px;
                object-fit: cover;
                border-radius: 3px;
            }
        }
    }
    &__slider {
        width: 518px;
        height: 433px;
        border-radius: 10px;
        margin-left: 35px;
        .lg-block({
            width: 450px;
            height: 400px;
        });
        .sm-block({
            width: 100%;
            order: 1;
            margin-left: 0;
        });
        .xs-block({
            height: 273px;
        });
    }
    &_row {
        .row-flex();
        margin-top: 27px;
        .md-block({
            justify-content: center;
        });
        .xs-block({
            margin-top: 35px;
        });
    }
    &_photo {
        .col();
        .size(6);
        .size-md(8);
        .size-sm(10);
        .size-xs(12);
        display: flex;
        align-items: flex-start;
        .sm-block({
           flex-direction: column;
        });
    }
    &_item {
        border-radius: 10px;
        .xs-block({
            height: 273px;
        });
        img {
            object-fit: cover;
            border-radius: 10px;
            margin: auto;
            .sm-block({
                max-height: 400px;
            });
            .xs-block({
                max-height: inherit;
                height: 273px;
            });
        }
    }
    &_text {
        .col();
        .size(6);
        .size-md(8);
        .size-sm(10);
        .size-xs(12);
        padding-left: 75px;
        .md-block({
            padding-left: 0;
        });
        .sm-block({
            margin-top: 44px;
        });
    }
    &_title {
        font-size: 36px;
        line-height: 44px;
        color: #414141;
        .b();
        .xs-block({
            font-size: 20px;
            line-height: 27px;
            max-width: 300px;
        });
    }
    &_flex {
        display: flex;
        align-items: center;
        &__1 {
            margin-top: 27px;
            .xs-block({
                margin-top: 15px;
            });
        }
        &__2 {
            margin-top: 49px;
            .xs-block({
                margin-top: 35px;
            });
        }
    }
    &_weigth {
        font-size: 17px;
        line-height: 24px;
        color: #5C5959;
        .m();
        .xs-block({
            font-size: 15px;
            line-height: 24px;
        });
    }
    &_yes {
        display: flex;
        align-items: center;
        margin-left: 45px;
        .xs-block({
            margin-left: 25px;
        });
        p {
            margin-left: 14px;
            font-size: 14px;
            line-height: 17px;
            color: #00A046;
            .xs-block({
                margin-left: 10px;
                font-size: 14px;
                line-height: 20px;
            });
        }
    }
    &_price {
        font-size: 25px;
        line-height: 30px;
        color: #E0001A;
        .b();
        .xs-block({
            font-size: 20px;
            line-height: 30px;
        });
    }
    &_btn {
        margin-left: 55px;
        display: inline-block;
        position: relative;
        padding: 17px 65px 17px 35px;
        background: #66BF01;
        box-shadow: 0px 8px 20px rgba(102, 191, 1, 0.3);
        border-radius: 4px;
        font-size: 16px;
        line-height: 1;
        text-align: center;
        text-decoration: none;
        .blick();
        .xs-block ({
            text-align: center;
            margin-left: 40px;
            padding: 14px 55px 14px 35px;
            font-size: 14px;
        });
        &::after {
            content: '';
            position: absolute;
            top: 15px; right: 28px;
            background: url(../img/basket.svg) center/cover no-repeat;
            width: 18px;
            height: 18px;
            .xs-block ({
                width: 13px;
                height: 13px;
                top: 13px; right: 30px;
            });
        }
        p {
            text-transform: uppercase;
            color: #FFFFFF;
            text-shadow: 0px 0px 4px rgba(0, 0, 0, 0.3);
            .ex();
            position: relative;
            z-index: 10;
        }
    }
    &_p {
        margin-top: 40px;
    }
    &_div {
        margin-bottom: 20px;
        font-size: 17px;
        line-height: 24px;
        color: #414141;
        display: flex;
        align-items: flex-start;
        .xs-block ({
            display: block;
            font-size: 15px;
            line-height: 22px;
            margin-bottom: 13px;
            padding-right: 15px;
        });
        p {
            margin-left: 7px;
            .xs-block ({
                margin: 7px 0 0;
            });
        }
    }
}

.slick-dotted.slick-slider {
    margin-bottom: 0;
}

.goods {
    margin-top: 80px;
    .md-block({
        margin-top: 30px;
    });
    &_row {
        .row-flex();
        .md-block({
            justify-content: center;
        });
    }
    &_left {
        .col();
        .size(6);
        .size-md(8);
        .size-sm(10);
        .size-xs(12);
        .md-block({
            order: 2;
            margin-top: 40px;
        });
    }
    &_right {
        .col();
        .size(6);
        .size-md(8);
        .size-sm(10);
        .size-xs(12);
        padding-left: 75px;
        .md-block({
            padding-left: 0;
            order: 1;
        });
    }
    &_title {
        .b();
        display: inline-block;
        font-size: 28px;
        line-height: 130%;
        color: #414141;
        padding-bottom: 15px;
        border-bottom: 3px solid #66BF01;
        .xs-block({
            font-size: 20px;
            padding-bottom: 10px;
        });
    }
    &_line {
        height: 2px;
        width: 100%;
        background-color: #DDDDDD;
        position: relative;
        top: -2px;
        z-index: -1;
        margin-bottom: 10px;
    }
    &_item {
        margin-top: 30px;
        font-size: 18px;
        line-height: 24px;
        color: #414141;
        .xs-block({
            font-size: 16px;
        });
        b {
            .xs-block({
                display: block;
               max-width: 320px;
            }); 
        }
        p {
            font-size: 17px;
            margin-top: 10px;
            .xs-block({
                font-size: 15px;
            });
        }
    }
    &_money {
        background: #F8F7F7;
        border-radius: 7px;
        width: 100%;
        padding: 30px 33px 35px;
        position: relative;
        .xs-block({
           padding: 27px 23px 25px; 
        });
        &::after {
            content: '';
            position: absolute;
            bottom: 0; right: 0;
            background: url(../img/money.png) center/cover no-repeat;
            width: 82px;
            height: 75px;
        }
        b {
            .ex();
            font-size: 18px;
            line-height: 24px;
            text-transform: uppercase;
            color: #414141;
            .xs-block({
                font-size: 16px;
            });
        }
        p {
            margin-top: 10px;
            font-size: 17px;
            line-height: 24px;
            color: #383535;
            .xs-block({
                font-size: 15px;
            });
        }
    }
    &_garant {
        margin-top: 23px;
        border: 1px solid #C1C1C1;
        border-radius: 7px;
        width: 100%;
        padding: 30px 33px 35px;
        .xs-block({
            padding: 27px 23px 25px; 
            margin-top: 35px;
        });
        b {
            .ex();
            font-size: 18px;
            line-height: 24px;
            text-transform: uppercase;
            color: #414141;
            .xs-block({
                font-size: 16px;
            });
        }
        p {
            margin-top: 10px;
            font-size: 17px;
            line-height: 24px;
            color: #383535;
            .xs-block({
                font-size: 15px;
            });
        }
    }
    &_quest {
        margin-top: 35px;
        font-size: 18px;
        line-height: 24px;
        color: #414141;
        .xs-block({
            display: none;
        });
    }
    &_a {
        display: inline-block;
        margin-top: 12px;
        font-size: 18px;
        line-height: 24px;
        color: #66BF01;
        .b();
        position: relative;
        padding-right: 15px;
        .xs-block({
            display: none;
        });
        &:hover {
            text-decoration: none;
        }
        &::after {
            content: '';
            position: absolute;
            top: 3px; right: 0;
            background: url(../img/right-arrow.svg);
            width: 13px;
            height: 13px;
        }
    }
}

.also {
    margin-top: 80px;
    background-color: #F9F9F9;
    padding: 40px 0 65px;
    .xs-block({
        margin-top: 50px;
        border-top: 3px solid #E7E7E7;
        background-color: #fff;
        padding-bottom: 0;
    });
    &_top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .sm-block({
            justify-content: center;
        });
    }
    &_arrows {
        display: flex;
        align-items: center;
        margin-top: 10px;
        .xs-block({
            display: none;
        });
    }
    &_arrow1 {
        cursor: pointer;
        circle {
            transition: .4s all;
        }
        &:hover circle {
            fill: #66BF01;
        }
    }
    &_arrow2 {
        cursor: pointer;
        circle {
            transition: .4s all;
        }
        &:hover circle {
            fill: #66BF01;
        }
    }
    &__slider {
        margin-top: 20px;
        .xs-block({
            display: flex;
            flex-wrap: wrap;
        });
        & > div {
            .xs-block({
                width: 50%;
            }); 
        }
    }
    &_card {
        height: 499px;
        padding: 30px 28px 29px 22px;
        background-color: #fff;
        border-radius: 6px;
        position: relative;
        transition: .4s all;
        margin: 0 14px;
        .xs-block({
            padding: 40px 0 34px;
            height: auto;
            margin: 0;
        });
        &:hover {
            box-shadow: 9px 12px 25px rgba(0, 0, 0, 0.07);
            .xs-block({
                box-shadow: none;
            });
        }
    }
}