.breadcrumb {
    margin-top: 15px;
    .md-block({
       display: none;
    });
    a {
        font-size: 14px;
        line-height: 17px;
        color: #949494;
        position: relative;
        padding-right: 16px;
        margin-right: 12px;
        text-decoration: none;
        &::after {
            content: '';
            position: absolute;
            top: 45%;
            transform: translateY(-50%);
            right: 0;
            width: 4px;
            height: 4px;
            background: #AFAFAF;
        }
        &:hover {
            text-decoration: underline;
        }
    }
    &_last {
        font-size: 14px;
        line-height: 17px;
        color: #414141;
        .m();
    }
}

.catalog-goods {
    margin-top: 33px;
    .catalog_row {
        .xs-block ({
            border-top: none;
            border-bottom: none;
            margin-top: 0;
        });
    }
    &__item {
        padding-top: 40px;
        &:not(:first-child) {
            padding-top: 80px;
            .xs-block ({
               padding-top: 40px;
            });
        }
    }
    &__title {
        .b();
        display: inline-block;
        font-size: 28px;
        line-height: 130%;
        color: #414141;
        padding-bottom: 15px;
        border-bottom: 3px solid #66BF01;
        .xs-block({
            font-size: 20px;
            padding-bottom: 10px;
        });
    }
    &__line {
        height: 2px;
        width: 100%;
        background-color: #DDDDDD;
        position: relative;
        top: -2px;
        z-index: -1;
        margin-bottom: 10px;
    }
}

.carding {
