.news {
    margin-top: 33px;
    &_row {
        .row-flex();
    }
    &_item {
        margin-top: 78px;
        text-decoration: none;
        .col();
        .size(3);
        .size-md(6);
        .xs-block({
            margin-top: 35px;
        });
        transition: .4s all;
        &:hover {
            cursor: pointer;
        }
        &:hover .news_item--title {
            color: #66BF01;
            text-decoration: underline;
        }
        &:hover .news_item--tag {
            color: #fff;
            background-color: #009CDE;
        }
        &:hover .news_item--news {
            color: #fff;
            background-color: #F2341F;
        }
        &:hover .news_item--blog {
            color: #fff;
            background-color: #66BF01;
        }
        &:hover .news_item--img::after {
            background: rgba(0, 0, 0, 0.29);
        }
        &--img {
            position: relative;
            &::after {
                content: '';
                position: absolute;
                top: 0; left: 0;
                width: 100%; height: 98%;
                border-radius: 6px;
                background: rgba(0, 0, 0, 0);
                transition: .4s all;
            }
        }
        &--date {
            position: absolute;
            left: 0; bottom: 21px;
            background: url(../img/date.png) center/cover no-repeat;
            width: 70px;
            height: 26px;
            .xs-block({
                bottom: 13px;
                width: 43.44px;
                height: 16.29px;
            });
            span {
                font-size: 14px;
                line-height: 29px;
                padding-left: 11px;
                color: #FFFFFF;
                .b();
                .xs-block({
                    font-size: 8.6881px;
                    line-height: 16.29px;
                });
            }
        }
        &--title {
            margin-top: 16px;
            font-size: 17px;
            line-height: 20px;
            color: #252525;
            transition: .4s all;
            .b();
            .xs-block({
                margin-top: 10px;
                font-size: 15px;
                line-height: 18px;
            });
        }
        &--p {
            margin-top: 10px;
            font-size: 15px;
            line-height: 20px;
            color: #575757;
            max-width: 275px;
            .xs-block({
                font-size: 13px;
                line-height: 16px;
                color: #575757;
                margin-top: 7px;
            });
        }
        &--tag {
            margin-top: 15px;
            display: inline-block;
            width: 115px;
            height: 24px;
            border: 1px solid #009CDE;
            border-radius: 30px;
            font-size: 14px;
            line-height: 24px;
            text-align: center;
            color: #009CDE;
            text-decoration: none;
            transition: .4s all;
            .xs-block({
                margin-top: 10px;
                width: 71px;
                height: 19px;
                border-radius: 18px;
                line-height: 19px;
                font-size: 11px;
            });
        }
        &--news {
            margin-top: 15px;
            display: inline-block;
            width: 115px;
            height: 24px;
            border: 1px solid #F2341F;
            border-radius: 30px;
            font-size: 14px;
            line-height: 24px;
            text-align: center;
            color: #F2341F;
            text-decoration: none;
            transition: .4s all;
            .xs-block({
                margin-top: 10px;
                width: 71px;
                height: 19px;
                border-radius: 18px;
                line-height: 19px;
                font-size: 11px;
            });
        }
        &--blog {
            margin-top: 15px;
            display: inline-block;
            width: 115px;
            height: 24px;
            border: 1px solid #66BF01;
            border-radius: 30px;
            font-size: 14px;
            line-height: 24px;
            text-align: center;
            color: #66BF01;
            text-decoration: none;
            transition: .4s all;
            .xs-block({
                margin-top: 10px;
                width: 71px;
                height: 19px;
                border-radius: 18px;
                line-height: 19px;
                font-size: 11px;
            });
        }
    }
}

