.video1 {
    background: url(../img/video.jpg) center/cover no-repeat;
    height: 444px;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    .xs-block({
        height: 280px;
    });
    iframe {
        border: none;
    }
    &-play {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 98px;
        height: 98px;
        border-radius: 100%;
        background: #FFFFFF;
        margin-left: 55px;
        transition: .4s all;
        cursor: pointer;
        &:hover {
            box-shadow: 8px 9px 19px rgba(0, 0, 0, 0.15);
            .play {
                transform: scale(1.1);
            }
        }
        .play {
            position: relative;
            left: 4px;
            transition: .4s all;
        }
    }
}

